<template>
  <div class="content invite">
    <div id="" class="content-title content-mg">
        {{ $t("finace.in.part1") }} 
    </div>
    <div class="coin-list clearfix content-pad">
      <el-select v-model="selectCoinID" class="coin pull-left" @change="handleSelect">
        <el-option
          v-for="item in selectList"
          :key="item.currency_id"
          :label="item.currency_mark" 
          :value="item.currency_id" v-if="item.is_allow_tibi == 1">
        </el-option>
      </el-select>
      <div class="pull-left currency_mark">{{ $t("finace.in.part2") }} {{ coinDetail.currency_mark }}: {{ coinDetail.num }}</div>
			<div class="pull-left currency_mark">{{ $t("finace.in.part3") }} {{ coinDetail.currency_mark }}: {{ coinDetail.forzen_num }}</div>
    </div>
    <!-- <p class="invite-code content-pad">{{ $t('inviteCode.myInviteCode') }} <span id="invite-code">{{ inviteDetail.invite_code }}</span></p> -->
    <div class="invite-detail content-pad" style="margin-bottom: 44px;">
        <div class="clearfix address">
        <div class="pull-left invite-owner">
          <p class="des">{{ $t("finace.in.part4") }} {{ coinDetail.currency_mark }} {{ $t("finace.in.part5") }} </p>
          <div class="clearfix">
            <div class="pull-left invite-url">{{ coinDetail.wallet_address }}&nbsp;</div>
            <a href="javascript:void(0)" class="pull-left copy-address" v-clipboard:copy="coinDetail.wallet_address" v-clipboard:success="handleCopySuccess" v-clipboard:error="handleCopyError">{{ $t("finace.in.part6") }} </a>
          </div>
        </div>
        <div class="pull-right">
          <div class="code" id="invite-url-qrcode"><img :src="coinDetail.wallet_address_qrcode"></div>
          <p>{{ coinDetail.currency_mark }} {{ $t("finace.in.part7") }} </p>
        </div>
      </div>
    </div>
    <div class="invite-rules content-pad">
      <div style="font-size: 13px;">{{ $t("finace.in.part8") }} </div>
      <p class="gray">{{ $t("finace.in.part9") }} {{ $t("finace.in.part10") }} </p>
      <p class="gray"> {{ $t("finace.in.part11") }} </p>
    </div>
    <div class="invite-table content-pad">
      <div class="content-title">{{ $t("finace.in.part12") }} </div>
        <el-table
        :data="tableData" class="data-table">
          <el-table-column :label="$t('finace.in.part13')" width="180">
            <template slot-scope="scope">
              <div class="time" style="color: #a9afbb">{{ scope.row.chongzhi_time }}</div>
            </template>
          </el-table-column>
          <el-table-column :label="$t('finace.in.part14')">
            <template slot-scope="scope">
              <div  class="wrap" :title="scope.row.tibi_url">{{ scope.row.tx_id }}</div>
              
            </template>
          </el-table-column>
          <el-table-column :label="$t('finace.in.part15')">
            <template slot-scope="scope">
              {{ scope.row.actual_money }}
            </template>
          </el-table-column>
          <el-table-column :label="$t('finace.in.part16')">
            <template slot-scope="scope">
              {{ scope.row.confirm_num }}
            </template>
          </el-table-column>
          <el-table-column :label="$t('finace.in.part17')">
            <template slot-scope="scope">
              {{ scope.row.arrival_time }}
            </template>
          </el-table-column>
          <el-table-column :label="$t('finace.in.part18')">
            <template slot-scope="scope">
              <div v-if="parseInt(scope.row.chongzhi_status) == 1">{{ $t("finace.in.part19")  }}</div>
              <div v-if="parseInt(scope.row.chongzhi_status) == 2">{{ $t("finace.in.part20")  }}</div>
              <div v-if="parseInt(scope.row.chongzhi_status) == 3">{{ $t("finace.in.part21")  }}</div>
            </template>
          </el-table-column>
      </el-table>
      <el-pagination
        background
        layout="total, prev, pager, next, jumper"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageOptions.page"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="pageOptions.per_page_num"
        :total="totalCount" class="page">
      </el-pagination>
    </div>
    
  </div>
</template>
<script>
  import { financeCenter } from '@/api/orderManage'
  import { assetTurnInList, assetTurnInTo } from '@/api/assetManage'
  export default {
    data() {
      return {
        coinDetail: {},
        coinList: null,
        selectCoinID: 0,
        selectList: [],
        // 分页选项
        pageOptions: {
          per_page_num: 10,
          page: 1
        },
        totalCount: 0,
        tableData: null
      }
    },
    created() {
      this.getSelectList()
    },
    methods: {
      handleSelect() {
        this.getUserInviteListDetail()
        this.getAssetTurnInTo()
        window.location.href = window.location.href.split('?')[0] + '?currency_id=' + this.selectCoinID
      },
      getSelectList() {
        financeCenter().then(res => {
          if (res.status !== 1000) {
            this.$message.error(res.msg)
          }
          this.selectList = [...res.data.normal_currency]
          if (this.$route.query.currency_id) {
            this.selectCoinID = parseFloat(this.$route.query.currency_id)
          } else {
            this.selectCoinID = this.selectList[0].currency_id
          }
          this.getAssetTurnInTo()
          this.getUserInviteListDetail()
        }).catch(() => {
        })
      },
      handleSizeChange(val) {
        this.pageOptions.page = val
        this.getmyAdList()
      },
      handleCurrentChange(val) {
        this.pageOptions.page = val
        this.getmyAdList()
      },
      getAssetTurnInTo() {
        const data = {
          'currency_id': this.selectCoinID
        }
        assetTurnInTo(data).then(res => {
          console.log(res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          this.coinDetail = res.data
        }).catch(() => {
  
        })
      },
      getUserInviteListDetail() {
        const data = {
          'length': this.pageOptions.per_page_num,
          'start': (this.pageOptions.page - 1) * 10,
          'status': -1
        }
        if (this.selectCoinID) {
          data.currency_id = this.selectCoinID
        }
        assetTurnInList(data).then(res => {
          console.log(res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          if (res.data !== '') {
            this.tableData = res.data.chongzhi_list
            this.totalCount = res.recordsTotal
          }
        }).catch(() => {
  
        })
      },
      handleCopySuccess() {
        this.$message.success(this.$t('errorTips.copySuccess'))
      },
      handleCopyError() {
        this.$message.error(this.$t('errorTips.copyError'))
      },
      copyUrl() {

      },
      add0(m) {
        return m < 10 ? '0' + m : m
      },
      format(shijianchuo) {
        // shijianchuo是整数，否则要parseInt转换
        var time = new Date(shijianchuo)
        var y = time.getFullYear()
        var m = time.getMonth() + 1
        var d = time.getDate()
        var h = time.getHours()
        var mm = time.getMinutes()
        var s = time.getSeconds()
        return y + '-' + this.add0(m) + '-' + this.add0(d) + ' ' + this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s)
      }
    }
  }
</script>
<style lang="less" scoped>
 @import '../../style/app';
  .content-mg{
    margin: 0 25px;
  }
  .coin-list{
    margin: 32px 0px 22px;
    // border: 1px solid #e6eaf0;
    height: 37px;
    .currency_mark{
      display: inline-block;
      margin: 0 0px 0 30px;
      line-height: 37px;
      width: 250px;
    }
  }
  .content{
    background: white;
    padding: 20px 0px;
    font-size: 14px;
    color: #3e4558;
    // color: @violet;
  }
  .invite .content-pad {
    padding-left: 25px;
    padding-right: 25px;
  }

  .content-title {
    padding-top: 9px;
    padding-bottom: 18px;
    border-bottom: 1px solid #e6eaf0;
    font-size: 16px;
  }
  .invite-detail .address {
    border: 1px solid #e6eaf0;
    width: 100%;
    .invite-owner{
      padding: 50px 0 36px 13px;
      font-size: 14px;
    }
  }
  .invite-detail .address p.des{
    color: #9194a4;
    margin-bottom: 20px;
  }
  .invite-detail  .address .pull-right .code img{
    width: 96px;
    height: 96px;
    margin-bottom: 6px;
  }
  .invite-detail  .address .pull-right{
    text-align: center;
    margin: 10px 20px 0 0;
    font-size: 12px;
  }

  .invite .invite-code{
    margin: 30px 0 20px;
    line-height: 27px;
    color: #666666;
  }

  .invite .invite-code span{
    color: #3e4558;
    font-size: 18px;
    margin-left: 15px;
  }

  .gray{
    color: #9397a0;
  }
  .invite-rules{
    margin: 44px 0 27px 0;
    font-size: 12px;
    div{
      margin: 10px 0;
    }
    p{
      padding-bottom: 10px;
    }
  }
  .split{
    background: #f7f7fa;
    height: 20px;
  }
  .nav-second .invite{
    padding-left: 0;
    padding-right: 0;
  }
  .invite  .content-pad{
    padding-left: 25px;
    padding-right: 25px;
  }
  .invite-count{
    padding-top: 30px;
    padding-bottom: 40px;
  }
  .invite-count .title{
    font-size: 16px;
    margin: 10px 0;
  }
  .invite-count p{
    font-size: 18px;
  }
  .invite-table{
    margin-bottom: 70px;
  }
  .invite-table .title{
    line-height: 40px;
    font-size: 16px;
  }

  .avatar-btns button {
      height: 35px;
      margin-left: 5px !important;
  }
  .row{
    display: flex;
    .col-md-6{
      flex: 1;
      vertical-align: middle;
      img{
        width: 21px;
        height: 21px;
        display: inline-block;
        float: left;
      }
      span{
        display: inline-block;
      }
    }
  }
  .invite-url{
    display: inline-block;
    color: #232a4a;
    font-size: 18px;
    // width: 400px;
    // overflow: hidden;
    // white-space: nowrap;
    // text-overflow: ellipsis;
  }
  .copy-address{
    color: #357ce1;
    font-size: 16px;
    margin-left: 15px;
  }
</style>

<style lang="less">
  .invite{
    .el-button--primary{
      display: inline-block;
      padding: 3px 12px;
      margin-right: 18px;
      border: 1px solid #1fa8f6;
      border-radius: 5px;
      font-size: 12px;
      margin-top: 4px;
    }
  }

</style>



